.promo {
  margin-bottom: 75px;
  display: flex;
  flex-wrap: nowrap;
  gap: 2vw;
  position: relative;

  @media (max-width: $tablet-width) {
    margin-bottom: 81px;
    min-height: 494px;
    justify-content: space-between;
  }

  @media (max-width: $mobile-width) {
    margin-bottom: 0;
    min-height: 425px;
    // display: flex;
    justify-content: center;
    flex-direction: column;
    flex-wrap: wrap;
    align-items: center;
  }
}

.promo__header {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  box-sizing: border-box;
  padding-left: 13vw;
  position: relative;
  width: 52vw;
  padding-top: 66px;

  @media (max-width: $tablet-width) {
    padding-left: 11.5vw;
    padding-top: 79px;
  }

  @media (max-width: $mobile-width) {
    padding-left: 0;
    width: 100%;
    align-content: center;
    order: 1;
    padding-top: 0;
    transform: translateY(-36px);
  }
}

.promo__header h1 {
  @include font-set(80px, 80px, 700);
  letter-spacing: 6px;
  text-transform: uppercase;
  color: $white;
  margin: 0;
  margin-bottom: 10px;
  width: 100%;

  @media (max-width: $tablet-width) {
    @include font-set(60px, 68px, 700);
    letter-spacing: 4px;
    width: 60vw;
    margin-bottom: 10px;
  }

  @media (max-width: $mobile-width) {
    @include font-set(40px, 48px, 700);
    letter-spacing: 3px;
    width: 92vw;
  }
}

.promo__header p {
  @include font-set(22px, 156.8%, 400);
  font-family: $secondary-font;
  color: $white;
  padding-left: 98px;
  margin-bottom: 43px;
  position: relative;
  width: 82%;
  min-height: 102px;

  &::before {
    content: "";
    position: absolute;
    width: 66px;
    height: 2px;
    background-color: $white;
    top: 19px;
    left: 3px;

    @media (max-width: $tablet-width) {
      top: 16px;
      width: 33px;
    }
  }

  @media (max-width: $tablet-width) {
    @include font-set(18px, 156.8%, 400);
    width: 356px;
    padding-left: 50px;
    margin-bottom: 42px;
  }

  @media (max-width: $mobile-width) {
    @include font-set(16px, 156.8%, 400);
    width: 72vw;
  }
}

.promo__button {
  @include font-set(16px, auto, 700);
  text-transform: uppercase;
  letter-spacing: 5%;
  color: $white;
  background-color: $blue;
  padding: 18px 58px;
  box-sizing: border-box;
  border-radius: 5px;
  margin-left: 97px;
  width: 407px;
  margin-bottom: 155px;
  transform: translateY(-32px);

  &:hover {
    opacity: 0.7;
  }

  @media (max-width: $tablet-width) {
    margin-left: 51px;
    width: 379px;
    padding: 19px 28px;
    letter-spacing: 1px;
    margin-bottom: 0;
  }

  @media (max-width: $mobile-width) {
    width: 287px;
    margin: 0 auto;
    display: none;
  }
}

.promo__button-mobile {
  @include font-set(16px, auto, 700);
  text-transform: uppercase;
  letter-spacing: 5%;
  color: $white;
  background-color: $blue;
  padding: 18px 58px;
  box-sizing: border-box;
  border-radius: 5px;
  margin-left: 97px;
  width: 407px;
  margin-bottom: 155px;
  transform: translateY(-32px);
  display: none;

  &:hover {
    opacity: 0.7;
  }

  @media (max-width: $tablet-width) {
    margin-left: 51px;
    width: 379px;
    padding: 19px 28px;
    letter-spacing: 1px;
    margin-bottom: 0;
  }

  @media (max-width: $mobile-width) {
    width: 89vw;
    margin: 0 auto;
    display: flex;
    justify-content: center;
  }
}

.promo__scroll {
  @include font-set(14px, auto, 400);
  font-family: $secondary-font;
  color: $white;
  opacity: 50%;
  transform: rotate(-90deg);
  transform-origin: left top;
  width: 100px;

  &:hover {
    opacity: 0.7;
  }

  @media (max-width: $tablet-width) {
    display: none;
  }
}

.promo__image {
  @media (max-width: $tablet-width) {
    width: 260px;
    height: 340px;
  }

  @media (max-width: $mobile-width) {
    order: 2;
    width: 301px;
    height: 220px;
    padding-left: 19px;
  }
}
