.modal {
  position: fixed;
  top: 137px;
  right: -16px;
  left: 0;
  margin: auto;
  background-color: $blue;
  color: $white;
  width: 490px;
  height: 605px;
  max-width: 100%;
  max-height: 100%;
  box-sizing: border-box;
  display: none;
  z-index: 3;
  border: none;
  overflow: auto;

  &--opened {
    display: block;
  }
}

.modal__wrapper {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  padding: 15px 25px 0;
  box-sizing: border-box;
}

.modal fieldset {
  border: none;
  padding: 0;
  margin: 0;
}

.modal__title {
  @include font-set(28px, 40px, 400);
  margin: 0;
  margin-bottom: 12px;
}

.modal__slogan {
  @include font-set(16px, 22px, 400);
  font-family: $secondary-font;
  margin: 0;
  width: 97%;
  margin-bottom: 33px;
}

.modal__contacts {
  gap: 30px;
  margin: 0;
}

.modal__send {
  gap: 24px;
}

.modal__contacts,
.modal__send {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
}

.modal__contacts input,
.modal__contacts textarea {
  background-color: $white-opacity;
  border: none;

  &:focus,
  &:hover {
    box-shadow: 0 0 0 1px $white inset;
  }
}

.modal__contacts input {
  width: 412px;
  height: 52px;
  box-sizing: border-box;
  outline: none;
  color: $black;
  text-indent: 17px;

  &::placeholder {
    @include font-set(16px, 22px, 400);
    font-family: $secondary-font;
    color: $white;
  }
}

.modal__contacts textarea {
  width: 412px;
  height: 104px;
  margin-bottom: 16px;
  outline: none;
  text-indent: 17px;
  padding-top: 12px;
  color: $black;
  box-sizing: border-box;
  resize: none;

  &::placeholder {
    @include font-set(16px, 22px, 400);
    font-family: $secondary-font;
    color: $white;
  }
}

.modal__button {
  width: 412px;
  cursor: pointer;
  letter-spacing: 1px;
  padding: 14px 0;

  &:hover {
    opacity: 0.7;
  }
}

.modal__check-label {
  @include font-set(14px, 15px, 400);
  font-family: $secondary-font;
  position: relative;
  cursor: pointer;
  width: 316px;
  padding-top: 8px;
  padding-left: 49px;
}

.modal__check:checked + label::before,
.modal__check + label::before {
  content: "";
  position: absolute;
  left: 3px;
  top: 10px;
  width: 16px;
  height: 16px;
  border: 1px solid $white;
  box-sizing: border-box;
}

.modal__check:checked + label::after {
  content: "";
  position: absolute;
  left: 7px;
  top: 14px;
  width: 8px;
  height: 4px;
  border-left: 1px solid $white;
  border-bottom: 1px solid $white;
  box-sizing: border-box;
  transform: rotate(-45deg);
}

.modal__check:invalid {
  transform: translateY(95px);
}

.modal__check:focus + label::before {
  outline: 1px solid $black;
}

.modal__close {
  border: none;
  outline: none;
  background-color: transparent;
  top: 8px;
  right: 0;
  cursor: pointer;

  &:focus {
    &::before,
    &::after {
      border: 1px solid $black;
    }
  }

  &::before,
  &::after {
    content: "";
    position: absolute;
    top: 30px;
    right: 20px;
    width: 22px;
    height: 1px;
    background-color: $white;
  }

  &::before {
    transform: rotate(45deg);
    box-shadow: none;
  }

  &::after {
    transform: rotate(-45deg);
  }
}

.modal-show .modal {
  display: block;
}

.modal-show .page-body__overlay {
  display: block;
}


dialog {
  background-color: $blue;
}

dialog[open]:not(:focus-within) {
  background-color: rgb(0, 178, 250);
  transition: background-color 0.01s ease;
}

.no-dialog dialog {
  display: none;
}

.no-dialog dialog[open] {
  display: block;
}
