.page {
  height: 100%;
}

.page-body {
  margin: 0 auto;
  height: 100%;
  position: relative;
}

.page-main {
  flex: 1 0 auto;
}

.page-body__overlay {
  display: none;
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background-color: $black-opacity;
}

.page-body__overlay--view {
  display: block;
}

.page-container {
  min-height: 878px;
  background: url("../img/backpromo-desktop@1x.png") top left no-repeat,
    url("../img/triangle-desktop@1x.png") top left no-repeat;
  background-size: cover;
  background-color: $dark;
  width: 100%;

  @media (min-resolution: 2dppx) {
    background: url("../img/backpromo-desktop@2x.png") top left no-repeat,
      url("../img/triangle-desktop@2x.png") top left no-repeat;
    background-color: $dark;
    background-size: cover;
  }

  @media (max-width: $tablet-width) {
    background: url("../img/backpromo-tablet@1x.png") top left no-repeat,
      url("../img/triangle-tablet@1x.png") top right no-repeat;
    width: 100%;
    min-height: 686px;
    background-size: cover;
    background-color: $dark;

    @media (min-resolution: 2dppx) {
      background: url("../img/backpromo-tablet@2x.png") top left no-repeat,
        url("../img/triangle-tablet@2x.png") top right no-repeat;
      background-color: $dark;
    }
  }

  @media (max-width: $mobile-width) {
    background: url("../img/backpromo-mobile@1x.png") top left no-repeat,
      url("../img/triangle-mobile@1x.png") top left right no-repeat;
    background-color: $dark;
    width: 100%;
    min-height: 647px;

    @media (min-resolution: 2dppx) {
      background: url("../img/backpromo-mobile@2x.png") top left no-repeat,
        url("../img/triangle-mobile@2x.png") top right  no-repeat;
      background-color: $dark;
      background-size: cover;
    }
  }
}
